<template>
  <div class="SunView">
    <div class="drag"     
    @mousedown="dragStart"
    @mousemove="dragMove"
      @mouseup="dragEnd"
    :style="{ transform: 'translate(' + offsetX + 'px, ' + offsetY + 'px)' }">>
      <div class="planetContainer"  @wheel="zoomContainer"
      ref="planetContainer" >
      <div class="planet" v-for="planet in planets" :key="planet.name" >
        <img :src="planet.image" :alt="planet.name" :style="{ width: planet.size, height: 'auto' }" @mouseover="showTextbox(planet.name)" @mouseleave="hideTextbox" />
        <div class="tooltip" v-show="isTextboxVisible && currentPlanet === planet.name" >&ensp;&ensp;&ensp;&ensp;{{ planet.description }}</div>
      </div>
    </div>
   </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isTextboxVisible: false,
      currentPlanet: null,
        // 初始化位置变量
      offsetX : 0,
      offsetY : 0,
      initialX : 0,
      initialY : 0,
      isDragging : false,
      scale : 1.0,
      zoomIncrement : 0.1,
      draggableElement:document.getElementsByClassName('drap'),
      planets: [
      { name: '水星', image: require('@/assets/水星.png'), description: '水星是太阳系的八大行星中最小且最靠近太阳的行星。轨道周期是87.9691天，116天左右与地球会合一次，公转速度远远超过太阳系的其它行星。水星是表面昼夜温差最大的行星，大气层极为稀薄无法有效保存热量，白天时赤道地区温度可达437°C，夜间可降至-172°C。水星的轴倾斜是太阳系所有行星中最小的（大约1⁄30度），但有最大的轨道偏心率。水星在远日点的距离大约是在近日点的1.5倍。水星表面遍布环形山，与月球和其他卫星相似，其地质在数十亿年来都处于非活动状态。', size:'100%' },
      { name: '金星', image: require('@/assets/金星.png'), description: '金星（英语、拉丁语：Venus），在太阳系的八大行星中，是从太阳向外的第二颗行星，轨道公转周期为224.7天，没有天然的卫星。金星在中国古代称为太白、明星或大嚣，早晨出现于东方称启明，晚上出现于西方称长庚。到西汉时期，《史记‧天官书》作者司马迁从实际观测发现太白为白色，与“五行”学说联系在一起，正式把它命名为金星。英文名称源自罗马神话的爱与美的女神维纳斯（Venus），古希腊人称为阿佛洛狄忒，也是希腊神话中爱与美的女神。金星的天文符号用维纳斯的梳妆镜来表示。', size:'100%' },
      { name: '地球', image: require('@/assets/earth.png'), description: '地球（英文名：Earth；拉丁文：Terra）是距离太阳1.5亿公里的第三颗行星，也是人类已知的唯一孕育和支持生命的天体。地球的表面大约 29.2% 是由大陆和岛屿组成的陆地，剩余的 70.8% 大部分被海洋、海湾和其他咸水体覆盖，也被湖泊、冰川、河流和其他淡水体覆盖，尤其冰川覆盖最多,它们共同构成了水圈。地球的大部分极地地区都被冰覆盖。地球外层分为几个刚性构造板块，它们在数百万年的时间里在地表迁移，而其内部仍然保持活跃。', size:'100%' },
      { name: '火星', image: require('@/assets/火星.png'), description: '火星（英语：Mars；拉丁语：Martis；天文符号：♂），是距离太阳第四近的行星，也是太阳系中仅次于水星的第二小的行星，为太阳系里四颗类地行星之一。欧洲古称火星为“马尔斯”火星有两个天然卫星：火卫一和火卫二，形状不规则，可能是捕获的小行星。根据观测的证据，在火星观察到类似地下水涌出的现象，南极冰冠有部分退缩，雷达数据显示两极和中纬度地表下存在大量的水冰。' , size:'100%'},
      { name: '木星', image: require('@/assets/木星.png'), description: '木星（Jupiter）是太阳系中距离太阳第五近的行星，也是太阳系中体积最大的行星。木星是颗巨行星，质量是太阳的千分之一，但却是太阳系其他行星质量总和的2.5倍。木星的主要成分是氢，但只占十分之一分子数量的氦，却占了总质量的四分之一；它可能有岩石核心和重元素，但没有可以明确界定的固体表面。由于快速地自转，木星的外观呈现扁球体。大气层依纬度成不同的区域带，在彼此的交界处有湍流和风暴作用着。最显著的例子就是大红斑，这是17世纪第一次被望远镜见到后就未曾停歇过的巨大风暴。环绕着木星的还有微弱的行星环和强大的磁层，截至2023年2月，木星已知有92颗卫星。', size:'100%' },
      { name: '土星', image: require('@/assets/土星-m.png'), description: '土星（英文：Saturn，拉丁文：Saturnus，符号：♄），是太阳系八大行星之一，到太阳的距离排在太阳系第六位。古代中国土星是中国古代人根据五行学说结合肉眼观测到的土星的颜色（黄色）来命名的，亦称之为镇星（常写作填星）。土星是气态巨行星，主要由氢组成，还有少量的氦与少量元素，内部的核心包括岩石和冰，外围由数层金属氢和气体包覆着。最外层的大气层在外观上通常情况下都是平淡的，虽然有时会有长时间存在的特征出现。土星的风速高达1800千米/时，风速明显比木星快。土星的行星磁场强度介于地球和更强的木星之间。土星有一个显著的行星环系统，主要的成分是冰的微粒和较少数的岩石残骸以及尘土。2023年确认的土星的卫星有145颗 。', size:'100%' },
      { name: '天王星', image: require('@/assets/天王星-1.png'), description: '天王星（英文：Uranus，天文符号：⛢，占星符号：♅），为太阳系八大行星之一，是太阳系由内向外的第七颗行星（18.37~20.08天文单位），是一颗在太阳系中离太阳第七近的青色行星，其体积在太阳系中排名第三（比海王星大），质量排名第四（小于海王星），几乎横躺着围绕太阳公转。封面图为旅行者2号飞掠天王星最近时拍摄的照片。天王星是第一颗使用望远镜发现的行星。威廉·赫歇尔在1781年3月13日于自宅庭院中发现了这颗行星。天王星和海王星的内部和大气构成和更巨大的气态巨行星（木星、土星）不同，天文学家设立了冰巨星分类来定义它们。天王星拥有27颗已知天然卫星，其中有5颗规模较大，另外还有13条较为暗弱的行星环。' , size:'100%'},
      { name: '海王星', image: require('@/assets/海王星.png'), description: '海王星（英语：Neptune，天文符号：♆）是太阳系八大行星之一。海王星的大气层的化学组成以氢分子和氦为主。此外，海王星大气中还有微量的甲烷，这是使行星呈蓝色的原因之一。海王星有着强烈的风暴，测量到的风速高达2400km/h。海王星云顶温度是-218摄氏度（55K），比天王星云顶温度稍高。据推测，海王星很可能有一个炽热的内部，其核心的温度约7000℃，和大多数已知的行星相似。海王星的质量稍大于天王星，密度稍大于天王星，而半径稍小于天王星。', size:'100%' },
      { name: '冥王星', image: require('@/assets/冥王星.png'), description: '冥王星（Pluto，小行星编号为134340，天文符号为♇）是柯伊伯带中的矮行星。冥王星是被发现的第一颗柯伊伯带天体，第一颗类冥天体，是太阳系内已知体积最大、质量第二大的矮行星。在直接围绕太阳运行的天体中，冥王星体积排名第9，质量排名第10。冥王星是体积最大的外海王星天体，其质量仅次于位于离散盘中的阋神星。与其他柯伊伯带天体一样，冥王星主要由岩石和冰组成，质量相对较小，仅有月球质量的1/6、月球体积的1/3。冥王星的轨道离心率及倾角皆较高。', size:'100%' },
      { name: '月球', image: require('@/assets/moon.png'), description: '月球（英文名：Moon，拉丁文：Luna）是围绕地球旋转的球形天体，同时也是地球的天然卫星。月球是太阳系中体积第五大的卫星，其平均半径约为1737.10千米，相当于地球半径的0.273倍；质量则接近7.342×10²²千克，相当于地球的0.0123倍。月球的表面布满了可能由小天体撞击形成的撞击坑。月球与地球的平均距离约38.44万千米，大约是地球直径的30倍。', size:'100%' },

    ]
    };
  },
  methods: {
    showTextbox(planet) {
      this.isTextboxVisible = true;
      this.currentPlanet = planet;
    },
    hideTextbox() {
      this.isTextboxVisible = false;
      this.currentPlanet = null;
    },
   



  // 鼠标按下时开始拖动
  dragStart(event) {
    this.isDragging = true;
      this.initialX = event.clientX - this.offsetX;
      this.initialY = event.clientY - this.offsetY;
  },

  // 鼠标移动时拖动元素
  dragMove(event) {

    if (this.isDragging) {
        // 计算新的偏移量
        this.offsetX = event.clientX - this.initialX;
        this.offsetY = event.clientY - this.initialY;
      }
  },
  dragEnd() {
      this.isDragging = false;
    },
  
  // 鼠标松开时停止拖动
  mouseUP() {
    this.isDragging = false;
  },

  // 鼠标滚轮实现放大缩小
  zoomContainer(event) {
    event.preventDefault();
    
    console.log(event.deltaY )
      const delta = event.deltaY 

      // 根据滚轮方向缩放比例
      if (delta > 0) {
        this.scaleContainer(0.9); // 向下滚动，缩小
      } else {
        this.scaleContainer(1.1); // 向上滚动，放大
      }
    },
    scaleContainer(scaleFactor) {
      const container = this.$refs.planetContainer;
      const currentScale = parseFloat(container.style.transform.replace('scale(', '')) || 1;
      const newScale = currentScale * scaleFactor;

      // 设置最小和最大缩放值
      if (newScale >= 0.1 && newScale <= 3) {
        container.style.transform = `scale(${newScale})`;
      }
    }
  }

  }

</script>

