}


.SunView img {
 
  width: 10%; /* 缩小图片至原大小的50% */
  height: auto; /* 根据宽度自动调整高度，保持比例 */

 
    -webkit-user-drag: none;
     
    -moz-user-drag: none;
     
    -ms-user-drag: none;
     
    user-drag: none;
}

.tooltip {
  position: absolute;
  top: 50%; /* 相对于图片定位 */
  left: 50%;
