  max-width: 70%;
  font-family: 'SimSun', '宋体', serif; /* 指定字体家族为宋体 */;
  height: auto; /* 可以根据内容自适应高度 */
  word-wrap: break-word; /* 自动换行 */
  word-break: break-all; /* 中文换行 */
  white-space: normal; /* 自动换行 */
}

img:hover + .tooltip {
  display: block; /* 悬浮时显示 */
}


</style>