<template>
  <div class="kg">
    <el-row>
      <el-col :span="4">
        <div class="grid-content bg-purple leftView">
          <el-row>
            <div class="title"> 节点筛选面板</div>
            <el-tree ref="tree" :data="nodes" @check="checkChange" show-checkbox node-key="id" id="selectView">
            </el-tree>
          </el-row>


        </div>
      </el-col>
      <!-- 图谱板块 -->
      <el-col :span="18" id="kgBox">
        <div class="grid-content bg-purple-light">
          <svg id="kgView" ref="kgView"></svg>

        </div>
      </el-col>
      <!-- 高点节点控制界面 -->
      <el-col :span="2">
        <div class="grid-content bg-purple-light">
          <div class="col-2 col" style="overflow: scroll;">
            <ul class="multi-column-list " id="showNodes">
              <li v-for="item in nodes" :key="item.id" :style="{ 'color': colorMatch[item.label] }"
                >
                <span :style="{ '-webkit-text-stroke-color': colorMatch[item.label] + '80' }">●</span>
                {{ item.label }}
              </li>
            </ul>
          </div>
        </div>
      </el-col>
   
    </el-row>
  </div>
</template>
<script setup>
import * as d3 from 'd3'
import { reactive, onMounted, ref, watch } from 'vue';
import { data,nodes } from '../../assets/js/sun.js'
import { colorMatch } from '../../assets/js/config.js'


const currentNodes = reactive([])
const width = ref("")
const height = ref("")
const currentKGData = reactive([])

const checkChange = (a, checkNodes) => {
  if(checkNodes.checkedNodes.length == 0){
    d3.select("#kgView").html("")
  }else{

    if(dataFilter(checkNodes.checkedNodes).length == 0){
      alert("当前节点没有图谱关系")
    }else{
      Object.assign(currentKGData, dataFilter(checkNodes.checkedNodes))
      console.log(currentKGData)
    }
  }
 
}
const valueInit = () => {
  width.value = document.getElementById('kgView').clientWidth
  height.value = document.getElementById('kgView').clientHeight
}
// 根据节点获取关系
const dataFilter = (nodeArr) => {
  const labels = nodeArr.map(node => node.label);
  return data.filter((item) => {
    return labels.includes(item['node_from']) || labels.includes(item['node_to']);
  });
}
// 根据关系获取节点
const nodesFilter = (dataArr) => {

  // 根据当前节点，获取当前节点的node格式数据
  const labels = getSetByTwoAttr(dataArr)
  return labels.map((item, index) => ({ "id": index, "label": item }));
}
// 获取links的所有节点数组
const getSetByTwoAttr = (dataArr) => {
  // 定义一个 Set 来存储唯一的属性值
  const uniqueValues = new Set();

  // 遍历对象数组，将 prop1 和 prop2 的值加入 Set 中
  dataArr.forEach(obj => {
    uniqueValues.add(obj['node_from']);
    uniqueValues.add(obj['node_to']);
  });

  // 将 Set 转换为数组（如果需要数组形式的话）
  return Array.from(uniqueValues);
}
// computed()
watch(currentKGData, (newRelation) => {
  Object.assign(currentNodes, nodesFilter(newRelation))
  console.log(currentKGData,currentNodes)

  GroupExplorer(newRelation, currentNodes)
}
);
onMounted(() => {
  // ... 
  valueInit()
  // GroupExplorer(data, nodes)
})

//  知识图谱生成
const GroupExplorer = (data, nodes) => {
  d3.select("#kgView").html("")
  console.log(data, "data")


  const links = data.map(d => ({
    source: d.node_from,
    target: d.node_to,
    relation: d.relation
  }));

  d3.selectAll(".tooltip").style("visibility", "hidden")
  // 获取 kgBox 元素的引用
  var kgBox = document.getElementById('kgBox');
  d3.select("#kgBox")
    .attr("width", width)
    .attr("height", height)


  // 获取 kgBox 元素的宽度和高度
  var width = kgBox.offsetWidth; // 宽度
  var height = kgBox.offsetHeight; // 高度


  
  var svg = d3.select("#kgView").append('g')
  .attr("transform", "translate(1px,2px)")
    // .attr("width", width)
    // .attr("height", height)

  svg.append("svg:defs")
    .append("svg:marker")
    .attr("id", "marker")
    .attr('viewBox', '0 -5 10 10')
    .attr("refX", 20)
    .attr("refY", 0)
    .attr('markerWidth', 5)
    .attr('markerHeight', 5)
    .attr('orient', 'auto')
    .append('svg:path')
    .attr('d', 'M0,-5L10,0L0,5')
    .attr("fill", "grey");

    svg.append("clipPath")
        .attr("id", "circleClip")
        .append("circle")
        .attr("r", 20);
  // Create a simulation with several forces.
  const simulation = d3.forceSimulation(nodes)
    .force("link", d3.forceLink(links).id(d => d.label).distance(200))
    .force('charge', d3.forceManyBody().strength(-100))
    // .force("charge", d3.forceManyBody())
    .force("center", d3.forceCenter(width / 2, height / 2))

    




  // Add a line for each link, and a circle for each node.
  const link = svg.append("g")
    .attr("stroke", "#ffffff")
    .attr("stroke-opacity", 0.6)
    .selectAll()
    .data(links)
    .join("line")
    .attr("stroke-width", '2')
    .attr("fill", "white")

  const node = svg.append("g")
    .selectAll()
    .data(nodes)
    .join("circle")
    .attr("r", 20)
    .attr("stroke-width", '10')
    .attr("stroke", d => {
      if(colorMatch[d['label']] )
      return colorMatch[d['label']] + "80"
        else
        return '#ffffff60'
    })
    .attr("fill", d => {
      if(colorMatch[d.label])
      return colorMatch[d.label]
    else
      return "#ffccff"
    })
    let text = svg.append('g')
          .attr('class', 'texts')
          .selectAll('text')
          .data(nodes)
          .enter().append('text')
          .attr("text-anchor", "middle")
          .attr("class","text")
          .style("font-size","10px")
          // .attr('clip-path', "url(#circleClip)")
          



  
  
            // 箭头
            let arrow = svg.append('g')
        .selectAll('.arrow')
        .data(links)
        .enter().append('path')
        .attr('class', 'arrow')
        .attr('fill', "#aaa");
        // 添加节点文字标签
  const lineText = svg.append("g")
    .selectAll("text")
    .data(links)
    .enter()
    .append("text")
    .attr("font-size", "10px")
    .attr("fill","white")
    .attr("stroke-white","1")
    .text(function (e) { return e.relation })
  .attr("text-anchor","middle");


  simulation.on("tick", ticked);
  function ticked() {
    link
      .attr("x1", d => d.source.x)
      .attr("y1", d => d.source.y)
      .attr("x2", d => d.target.x)
      .attr("y2", d => d.target.y);

    node
      .attr("cx", d => d.x)
      .attr("cy", d => d.y);
    lineText
            .attr("x", d => (d.source.x + d.target.x) / 2)
            .attr("y", d => (d.source.y + d.target.y) / 2)
            .attr("transform", d => {
                const x = (d.source.x + d.target.x) / 2;
                const y = (d.source.y + d.target.y) / 2;
                const angle = Math.atan2(d.target.y - d.source.y, d.target.x - d.source.x) * 180 / Math.PI;
                let angle2=Math.abs(angle)>90?(angle+180):angle;
                return `rotate(${angle2},${x},${y})`;
            })
            .text(d=>{
            
             return d.relation
            });
            arrow
            .attr('d', d => getpath(drawarrow(d.source.x,d.source.y,d.target.x,d.target.y,10)))
            text
            .attr('x', d => d.x)
            .attr('y', d => d.y)
      
            .text(d => d.label);
          


  }
  var zoom = d3.zoom()
        .scaleExtent([0.1, 10]) // 鼠标缩放的距离, 范围
        .on('start', () => { // zoom 事件发生前 将变小手
            console.log('start')
            d3.select('#kgView').style('cursor', 'pointer')
        })
        .on('zoom', (e) => {
            console.log(e)
          d3.select('#kgView').select('g').attr('transform',
                'translate(' + e.transform.x + ',' + e.transform.y + ') scale(' + e.transform.k + ')'
            )
        })
        .on('end', () => {
          d3.select('#kgView').style('cursor', 'default')
        });
 
        d3.select('#kgView').call(zoom).call(zoom.transform, d3.zoomIdentity.scale(1))
  console.log(simulation, link, node)
  node.append("title")
    .text(d => d.label);

  // Add a drag behavior.
  node.call(d3.drag()
    .on("start", dragstarted)
    .on("drag", dragged)
    .on("end", dragended));
    const getpath = d3.line()

    function drawarrow(x1,y1,x2,y2,m){
        // Step 1: Calculate the midpoint
        const xm = (x1 + x2) / 2;
        const ym = (y1 + y2) / 2;

        // Step 2: Calculate the direction vector of the given line
        const dx = Math.abs(x2 - x1);
        const dy = Math.abs(y2 - y1);
        const dx2 = x2 - x1;
        const dy2 = y2 - y1;
        // Step 3: Calculate the length of the direction vector
        const len = Math.sqrt(dx * dx + dy * dy);

        // Step 4: Normalize the perpendicular vector and scale to half length of m
        const perpDx = dy2 * m / (2 * len);
        const perpDy = -dx2 * m / (2 * len);

        // Step 5: Calculate the endpoints of the perpendicular line
        let l=0.866*m;
        const cosTheta = dx / len;
        const sinTheta = dy / len;
        const x3 = xm + perpDx;
        const y3 = ym + perpDy;
        const x4 = xm - perpDx;
        const y4 = ym - perpDy;
        let x5=x2>x1?xm+cosTheta*l:xm-cosTheta*l;
        let y5=y2>y1?ym+sinTheta*l:ym-sinTheta*l;
        return [[x3,y3],[x4,y4],[x5,y5]];
      }
  function dragstarted(event) {
    if (!event.active) simulation.alphaTarget(0.3).restart();
    event.subject.fx = event.subject.x;
    event.subject.fy = event.subject.y;
  }

  // Update the subject (dragged node) position during drag.
  function dragged(event) {
    event.subject.fx = event.x;
    event.subject.fy = event.y;
  }

  // Restore the target alpha so the simulation cools after dragging ends.
  // Unfix the subject position now that it’s no longer being dragged.
  function dragended(event) {
    if (!event.active) simulation.alphaTarget(0);
    event.subject.fx = null;
    event.subject.fy = null;
  }

  // When this cell is re-run, stop the previous simulation. (This doesn’t
  // really matter since the target alpha is zero and the simulation will
  // stop naturally, but it’s a good practice.)
  // invalidation.then(() => simulation.stop());







}

</script>

